amiento puede generar atracción de vectores, lo que conlleva a tener también una amenaza desde el punto de vista biológico.")]),_c('p',{staticClass:"w-100"},[_vm._v("Debido a que cada vivienda cuenta con una amenaza propia de las condiciones del ecosistema donde se ubica resulta necesario establecer un perfil epidemiológico, de este modo es más fácil controlar la magnitud de la amenaza si de antemano se conoce que una población está expuesta, por ejemplo al dengue o a la malaria, en consecuencia se pueden desarrollar capacitaciones de identificación de los síntomas de las enfermedades y realizar acciones de prevención por ejemplo, evitando que los pobladores almacenen agua en tanques sin tapa.")])]),_c('li',{staticClass:"d-flex flex-wrap",attrs:{"data-aos":"fade-up"}},[_c('i',{staticClass:"fas fa-circle",staticStyle:{"color":"#008B8A"}}),_c('p',{staticClass:"fw-bold"},[_vm._v("Vulnerabilidad")]),_c('p',{staticClass:"w-100"},[_vm._v("Son las condiciones de susceptibilidad o falta de capacidad para enfrentar una amenaza. El riesgo a la salud de los habitantes se da cuando la vivienda no cumple con las condiciones adecuadas.")]),_c('p',{staticClass:"w-100"},[_vm._v("La vulnerabilidad se da en varios aspectos del mismo modo que las amenazas, por consiguiente, existe vulnerabilidad física, química, biológica y social.")]),_c('p',{staticClass:"w-100"},[_vm._v("Cuando los materiales de la vivienda no soportan los desastres naturales o se encuentra ubicada por debajo del nivel del río, se habla de viviendas vulnerables desde la perspectiva física. Un inmueble que no cuenta con acceso a agua potable o se encuentra cerca de depósitos de residuos peligrosos presenta vulnerabilidad química. Aquellas que están cerca de rellenos sanitarios o de lugares de estancamiento de aguas tendría vulnerabilidades biológicas y una que se encuentre dentro de zonas donde se presenta conflicto armado tendrían una vulnerabilidad social.")])]),_c('li',{staticClass:"d-flex flex-wrap",attrs:{"data-