")]),_c('p',{staticClass:"w-100"},[_vm._v("Del mismo modo, se puede presentar una caracterización de riesgo medio, cuando la vulnerabilidad es alta, pero la amenaza es baja o viceversa. Un ejemplo de esto sería la misma casa de lata y madera, pero ubicada en una zona donde no hay huracanes, tormentas fuertes, deslizamientos, ni ningún desastre natural registrado. En este ejemplo se tiene una vulnerabilidad alta, pero la amenaza es baja, como consecuencia la exposición es media.")]),_c('p',{staticClass:"w-100"},[_vm._v("Cuando no existe ni vulnerabilidad ni amenaza se conoce como caracterización de riesgo bajo. Siguiendo con el ejemplo, sería una vivienda construida en concreto, con columnas, vigas y paredes resistentes y geográficamente ubicada lejos de cualquier desastre natural registrado.")])]),_c('li',{staticClass:"d-flex flex-wrap",attrs:{"data-aos":"fade-up"}},[_c('i',{staticClass:"fas fa-circle",staticStyle:{"color":"#008B8A"}}),_c('p',{staticClass:"fw-bold"},[_vm._v(" Manejo o mitigación del riesgo ")]),_c('p',{staticClass:"w-100"},[_vm._v("Querer establecer una vivienda saludable debe abordar la mitigación del riesgo a través de procesos de mejoramiento de la vivienda; estos procesos deben ir acompañados de la atención primaria en salud, mantener infraestructura hospitalaria cerca de la vivienda y condiciones que permitan el desarrollo de las personas libre de enfermedad. Lo siguiente paso es cortar los ciclos de transmisión de enfermedades y prevención de enfermedades crónicas.")]),_c('p',{staticClass:"w-100"},[_vm._v("Las estrategias de vivienda saludable se deben promulgar a toda la familia y a la comunidad para generar factores protectores de la salud y que entre todos disminuyan los factores de riesgo. Desde las instituciones gubernamentales se deben establecer políticas que permitan mantener la salud de todos los ciudadanos y así mismo cada ciudadano es responsable del mejoramiento de su vivienda para mantener al máximo su salud.")])])])])}]

